.App {
  text-align: center;
}


.White-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 20px;
}

.Logo {
  height: 50px;
}

.Text-nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Text-nav-container h4 {
  margin-right: 10px; /* Join과 Login 사이의 간격을 조절합니다. */
}

.RegistPage{
  padding: 50px;
}

.Contents{
  border: 2px solid #333; /* 선의 두께와 색상을 설정합니다. */
  padding: 20px; /* 내용과 선 사이의 간격을 조절합니다. */
  width: 1000px; /* 원하는 가로 크기를 설정합니다. */
  margin: 50px auto; /* 화면 가운데 정렬을 위한 margin 설정 */
}

.Element {
  width: 300px;
  margin: 0 auto;
}

.InputElement {
  list-style: none;
  padding: 0;
}

.InputElement li {
  display: block;
  text-align: left;
  margin-bottom: 15px;
  
}

label {
  display: block;
  margin-bottom: 5px;
}

input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.InputElement label{
  margin-right: 10px;
}

.InputElement input{
  margin-right: 10px;
}

/* GenderInput.css */

.gender-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gender-options {
  display: flex;
  gap: 10px; /* 각 라디오 버튼 사이의 간격 조절 */
}